import React,{useContext} from 'react';

import { ThemeContext } from '../../contexts/ThemeContext';

import { servicesData } from '../../data/servicesData';
import { servicesDataEn } from '../../data/servicesData';

import './Services.css'
import SingleService from './SingleService/SingleService';

function Services() {

    const { theme, lang } = useContext(ThemeContext);

    return (
        <>
            {servicesData.length > 0 && (
                <div className="services" id="services" style={{backgroundColor:theme.secondary}}>
                    <div className="services-header">
                        <h1 style={{color: theme.primary}}>{lang === 'es' ? 'Servicios' : 'Services' }</h1>
                    </div>
                    <div className="services-body">
                        <p style={{color:theme.tertiary80}}>
                        {lang === 'es' ? 'Ofrezco servicios de investigacin cientfica y desarrollo en los siguientes campos. Me ilusiona un proyecto en el que pueda aportar valor con mis dos reas de formacin y que me permita crecer profesional y personalmente afrontando nuevos retos y tecnologas.' : 'I offer scientific research and development services in the following fields. I am excited about a project in which I can add value with my two professional areas to grow professionally and personally, facing new challenges and technologies.'}
                        </p>
                        <div className="services-bodycontainer">
                        {lang === 'es' ? 
                            servicesData.map(services => (
                                <SingleService
                                key={services.id}
                                id={services.id}
                                title={services.title}
                                icon={services.icon}/>
                            ))
                            :
                            servicesDataEn.map(services => (
                                <SingleService
                                key={services.id}
                                id={services.id}
                                title={services.title}
                                icon={services.icon}/>
                            ))} 
                        </div>
                    </div>
                </div>
            )}
       </>
    )
}

export default Services
