import React, { useContext } from 'react';

import './About.css';
import { ThemeContext } from '../../contexts/ThemeContext';
import { aboutData, aboutDataEn } from '../../data/aboutData'



function About() {

    const { theme } = useContext(ThemeContext);
    const { lang } = useContext(ThemeContext);
    return (
        <div className="about" id="about" style={{backgroundColor: theme.secondary}}>
            <div className="line-styling">
              <div className="style-circle" style={{backgroundColor: theme.primary}}></div>
              <div className="style-circle" style={{backgroundColor: theme.primary}}></div>
              <div className="style-line" style={{backgroundColor: theme.primary}}></div>
            </div>
            <div className="about-body">
                <div className="about-description">
                    <h2 style={{color: theme.primary}}>{lang === 'es' ? aboutData.title : aboutDataEn.title }</h2>
                    <p style={{color:theme.tertiary80}}>{lang === 'es' ? aboutData.description1 : aboutDataEn.description1 }<br/><br/>{lang === 'es' ? aboutData.description2 : aboutDataEn.description2 }</p>
                </div>
                <div className="about-img">
                    <img 
                        src={aboutData.image === 1 ? theme.aboutimg1 : theme.aboutimg2}  
                        alt="" 
                    />
                </div>
            </div>
        </div>

    )
}

export default About
