import React, { useState, useContext } from "react";
import { makeStyles, Menu, MenuItem, Button } from "@material-ui/core";
import { KeyboardArrowDownSharp } from "@material-ui/icons";
import { ThemeContext } from '../../contexts/ThemeContext';
import "./flag-icon-css/css/flag-icons.min.css";
import { createTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/core';

const theme = createTheme({
    status: {
        danger: '#e53e3e',
      },
      palette: {
        primary: {
          main: '#e7ad42',
          darker: '#e7ad42',
        },
        secondary: {
            main: '#e7ad42',
            darker: '#e7ad42',
        },
        neutral: {
          main: '#e7ad42',
          contrastText: '#fff',
        },
      },
});

const languages = [
    {
        code: "es",
        name: "ESP",
        country_code: "es",
    },
    {
        code: "en",
        name: "ENG",
        country_code: "us",
    },
];

const LangSelector = (props) => {
    const { lang, changeLang } = useContext(ThemeContext);
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = useState(null);
    const handleClose = (code) => {
        changeLang(code);
        setAnchorEl(null);
        if (props.onClose) {
            props.onClose();
        }
    };

    const currentLanguage = languages.find((elem) => elem.code === lang);

    return (
        <ThemeProvider theme={theme}>
        <div {...props}>
            
            <Button variant="contained" color="neutral" onClick={(e) => setAnchorEl(e.currentTarget)}>
                {currentLanguage && (
                    <>
                        <span className={`flag-icon flag-icon-${currentLanguage.country_code} ${classes.flagIcon}`} />
                        {currentLanguage.name}
                    </>
                )}
                <KeyboardArrowDownSharp style={{ color: "black" }} />
            </Button>
           
            <Menu
                id="profile-menu"
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={() => setAnchorEl(null)}
                keepMounted
                elevation={0}
                getContentAnchorEl={null}
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "center",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "center",
                }}
                className={classes.profileMenu}
                disableScrollLock
            >
                <MenuItem className={classes.menuItem} onClick={() => handleClose("es")}>
                    <span className={`flag-icon flag-icon-es ${classes.flagIcon}`} />
                    ESP
                </MenuItem>
                <MenuItem className={classes.menuItem} onClick={() => handleClose("en")}>
                    <span className={`flag-icon flag-icon-us ${classes.flagIcon}`} />
                    ENG
                </MenuItem>
            </Menu>
        </div>
        </ThemeProvider>
    );
};

const useStyles = makeStyles(() => ({
    profileMenu: {
        "& .MuiPaper-root": {
            backgroundColor: "#e7ad42",
            marginTop: "2px",
            boxShadow: '0 0 1px 0 rgba(0,0,0,0.31), 0 3px 4px -2px rgba(0,0,0,0.25)',
        },
    },
    menuItem: {
        "&:hover": {
            backgroundColor: "#a3a29d",
            text: "rgb(230,230,230)"
        },
    },
    flagIcon: {
        marginRight: "5px",
    },
}));

export default LangSelector;